*{
  font-family: Helvetica, sans-serif;
}

body{
  padding: 10px 60px;
}

input{
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}

h1{
  text-align: center;
  margin-top: 0;
}

footer{
  text-align: center;
}